import React, { Suspense, lazy } from "react";
import _ from "lodash";
import { Tooltip, Spinner } from "@salesforce/design-system-react";

const GRAXEnvSettingCombobox = lazy(() => import("./GRAXEnvSettingCombobox"));
const GRAXEnvSettingSwitch = lazy(() => import("./GRAXEnvSettingSwitch"));
const GRAXEnvSettingInput = lazy(() => import("./GRAXEnvSettingInput"));

export default class GRAXEnvSetting extends React.PureComponent {
  constructor(props) {
    super(props);

    if (this.props.id) {
      this.id = this.props.id;
    } else {
      this.id = Date.now().toString();
    }

    this.state = {
      inputValue: "",
      selection: null,
      selectedOption: null
    };
  }

  getLabelFromName(name) {
    return _.startCase(_.camelCase(name));
  }

  onChangeTrigger(value) {
    const onChange = this.props.onChange || this.props.onBlur || (() => {});

    onChange(value);
  }

  getType() {
    return this.props.setting.type || "text";
  }

  getLabel() {
    if (this.props.setting.label) {
      return this.props.setting.label;
    }

    return this.getLabelFromName(this.props.setting.name);
  }

  getDefaultValue() {
    return this.props.setting.defaultValue || this.props.defaultValue;
  }

  getHelpTextTooltip() {
    if (this.props.setting.helpText) {
      return (
        <Tooltip
          key={this.id + "-helptext"}
          id={this.id + "-helptext"}
          align="top left"
          content={this.props.setting.helpText}
        />
      );
    }

    return null;
  }

  getComponentToRender = ({ label, type, defaultValue }) => {
    if (type === "combobox") {
      return (
        <GRAXEnvSettingCombobox
          key={this.id}
          id={this.id}
          label={label}
          setting={this.props.setting}
          onChange={v => this.onChangeTrigger(v)}
          defaultValue={defaultValue}
        />
      );
    }

    if (type === "boolean") {
      return (
        <GRAXEnvSettingSwitch
          key={this.id}
          id={this.id}
          label={label}
          setting={this.props.setting}
          helpTextTooltip={this.getHelpTextTooltip()}
          onChange={v => this.onChangeTrigger(v)}
          defaultValue={defaultValue}
        />
      );
    }

    return (
      <GRAXEnvSettingInput
        key={this.id}
        id={this.id}
        type={type}
        label={label}
        setting={this.props.setting}
        helpTextTooltip={this.getHelpTextTooltip()}
        onChange={v => this.onChangeTrigger(v)}
        defaultValue={defaultValue}
      />
    );
  };

  render() {
    if (this.props.setting == null || this.props.id == null) return null;

    return (
      <Suspense fallback={<Spinner size="small" variant="brand" />}>
        {this.getComponentToRender({
          label: this.getLabel(),
          type: this.getType(),
          defaultValue: this.getDefaultValue()
        })}
      </Suspense>
    );
  }
}
