/* eslint-disable no-useless-escape */
import React from 'react';
import _ from 'lodash';
import moment from 'moment';

import {Spinner, Combobox, Input, Datepicker, Button, Checkbox, comboboxFilterAndLimit} from '@salesforce/design-system-react';
import GRAXSFieldCombobox from './GRAXSFieldCombobox';

// import {comboboxFilterAndLimit} from '../lib/utils';

var sanitizeElasticFilterValue = function(filterValue) {
    return filterValue
        .replace(/[\*\+\-=~><\"\?^\${}\(\)\:\!\/[\]\\\s]/g, '\\$&') // replace single character special characters
        .replace(/\|\|/g, '\\||') // replace ||
        .replace(/\&\&/g, '\\&&') // replace &&
        .replace(/AND/g, '\\A\\N\\D') // replace AND
        .replace(/OR/g, '\\O\\R') // replace OR
        .replace(/NOT/g, '\\N\\O\\T'); // replace NOT
};

const FIELD = 'Field';
const OPERATOR = 'Operator';
const FILTER_VALUE = 'Filter Value';
const IS_ACTIVE = 'Is Active?';

const TYPE_VS_OPERATORS = {
    'text': [
        {id: 'eq', label: '==', isNegated: false},
        {id: 'eq', label: '!=', isNegated: true},
        {id: 'contains', label: 'contains', isNegated: false},
        {id: 'contains', label: 'not contains', isNegated: true},
        {id: 'in', label: 'in', isNegated: false},
        {id: 'in', label: 'not in', isNegated: true},
        {id: 'exists', label: 'exists', isNegated: false, type: 'boolean'}
    ],
    'date': [
        {id: 'eq', label: '==', isNegated: false},
        {id: 'eq', label: '!=' , isNegated: true},
        {id: 'gt', label: '>', isNegated: false},
        {id: 'lt', label: '<', isNegated: false},
        {id: 'gte', label: '>=', isNegated: false},
        {id: 'lte', label: '<=', isNegated: false},
        {id: 'exists', label: 'exists', isNegated: false, type: 'boolean'}
    ],
    'number': [
        {id: 'eq', label: '==', isNegated: false},
        {id: 'eq', label: '!=', isNegated: true},
        {id: 'gt', label: '>', isNegated: false},
        {id: 'lt', label: '<', isNegated: false},
        {id: 'gte', label: '>=', isNegated: false},
        {id: 'lte', label: '<=', isNegated: false},
        {id: 'exists', label: 'exists', isNegated: false, type: 'boolean'}
    ],
    'long': [
        {id: 'eq', label: '==', isNegated: false},
        {id: 'eq', label: '!=', isNegated: true},
        {id: 'gt', label: '>', isNegated: false},
        {id: 'lt', label: '<', isNegated: false},
        {id: 'gte', label: '>=', isNegated: false},
        {id: 'lte', label: '<=', isNegated: false},
        {id: 'exists', label: 'exists', isNegated: false, type: 'boolean'}
    ],
    'boolean': [
        {id: 'eq', label: '==', isNegated: false},
        {id: 'exists', label: 'exists', isNegated: false}
    ]
};


export class GRAXFilterableCombobox extends React.Component {
    constructor(props) {
		super(props);

        this._isMounted = false;

		this.state = {
			inputValue: '',
            selection: [],
            isOpened: false
		};
    }

    componentDidMount() {
        this._isMounted = true;

        if(this.props.selection) {
            console.log(this.props.selection);

            this.setState({
                selection: this.props.selection
            });
        }
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    filterOptionsHandler() {
        var options = this.props.options || [];

        if(this.props.filterOptionsHandler && this.state.isOpened) {
            options = this.props.filterOptionsHandler(options);
        }

        return comboboxFilterAndLimit({
            inputValue: this.state.inputValue,
            limit: 1000,
            options: options,
            selection: this.state.selection
        })
    }

    render() {
        return (
            <React.Fragment>
                <Combobox
                    classNameMenu={'scrollable-GRAXFilterableCombobox'}
                    events={{
                        onChange: (event, { value }) => {
                            this.setState({
                                inputValue: value
                            });

                            if (this.props.onChange) {
                                this.props.onChange(event, value);
                            }
                        },
                        onRequestRemoveSelectedOption: (event, data) => {
                            if(this._isMounted) {
                                this.setState({
                                    inputValue: '',
                                    selection: data.selection,
                                });
                            }

                            if (this.props.onRequestRemoveSelectedOption) {
                                this.props.onRequestRemoveSelectedOption(event, data.selection);
                            }
                        },
                        onSelect: (event, data) => {
                            if(this._isMounted) {
                                this.setState({
                                    inputValue: '',
                                    selection: data.selection,
                                });
                            }

                            if (this.props.onSelect) {
                                this.props.onSelect(event, data.selection);
                            }
                        },
                        onOpen: (event, data) => {
                            if(this._isMounted) {
                                this.setState({
                                    isOpened: true
                                });
                            }
                        },
                        onClose: (event, data) => {
                            if(this._isMounted) {
                                this.setState({
                                    isOpened: false
                                });
                            }
                        }
                    }}
                    labels={{
                        placeholder: this.props.placeholder,
                    }}
                    options={this.filterOptionsHandler()}
                    selection={this.state.selection}
                    value={this.state.inputValue}
                    variant={this.props.variant || 'inline-listbox'}
                    required={true}
                    multiple={false}
                />
            </React.Fragment>
        );
    }
}


export default class GRAXFilterGeneration extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            _filterValues: null,
            _error: null,
            _filterValuesFields: []
        };
    }

    isLoaded() {
        return this.state._error || (this.props.filterableFieldOptions != null);
    }

    getFilterValueStruct() {
        return {
            id: null,
            value: null,
            operator: 'eq',
            isNegated: false,
            isActive: true,
            selectedField: null
        };
    }

    getFilterValuesList(_filterValues) {
        return _.map(_filterValues, (filterValue, fieldName) => {
            var filterValueStruct = this.getFilterValueStruct();

            filterValueStruct.id = fieldName;
            filterValueStruct.value = filterValue.value;
            filterValueStruct.operator = filterValue.operator;
            filterValueStruct.isNegated = filterValue.isNegated;
            filterValueStruct.isActive = filterValue.isActive;
            filterValueStruct.selectedField = _.filter(this.props.filterableFieldOptions, (option, i) => {
                return option && option.id === fieldName;
            })[0] || null;

            return filterValueStruct;
        });
    }

    componentDidMount() {
        var _filterValues = this.props.filterValues || {};
        var _filterValuesFields = Object.keys(_filterValues);

        this.setState({
            _filterValues: _filterValues,
            _filterValuesFields: _filterValuesFields,
            _filterValuesList: this.getFilterValuesList(_filterValues)
        });

        this.props.onFilterValueChangeHandler(this.getFilterFunction.bind(this), _filterValuesFields, this.getFilterQuery.bind(this), this.state._filterValues);
    }


    onFilterValueChangeHandler(filterValue, selection) {
        var _filterValues = this.state._filterValues;

        if(selection && selection.length > 0) {
            var selectedField = selection[0] || null;

            filterValue.id = selectedField.id;
            filterValue.selectedField = selectedField;

            _filterValues[filterValue.id] = {
                value: filterValue.value,
                isActive: filterValue.isActive,
                operator: filterValue.operator,
                isNegated: filterValue.isNegated,
                selectedField: selectedField
            };
        } else {
            delete _filterValues[filterValue.id];
            filterValue.id = null;
            filterValue.selectedField = null;
        }

        var _filterValuesFields = Object.keys(_filterValues);

        this.setState({
            _filterValues: _filterValues,
            _filterValuesFields: _filterValuesFields,
            _filterValuesList: this.state._filterValuesList
        });

        if(this.props.onFilterValueChangeHandler) {
            this.props.onFilterValueChangeHandler(this.getFilterFunction.bind(this), _filterValuesFields, this.getFilterQuery.bind(this), this.state._filterValues);
        }
    }

    addAnotherFieldHandler() {
        var _filterValuesList = this.state._filterValuesList;

        if(_filterValuesList.length === 0 || _filterValuesList[_filterValuesList.length - 1].id != null) {
            _filterValuesList.push(this.getFilterValueStruct());

            this.setState({
                _filterValuesList: _filterValuesList
            });
        }
    }

    removeFieldHandler(i) {
        var _filterValues = this.state._filterValues;
        var _filterValuesList = this.state._filterValuesList;

        delete _filterValues[_filterValuesList[i].id];
        _filterValuesList.splice(i, 1);

        this.setState({
            _filterValues: _filterValues,
            _filterValuesFields: Object.keys(_filterValues),
            _filterValuesList: _filterValuesList
        });
    }

    getFilterFunction(dataList) {
        return _.filter(dataList, (item, i) => {
            var isReturned = true;

            _.forEach(this.state._filterValues, (filterValue, fieldName) => {
                if(!filterValue.isActive) return;

                var filterOperator = filterValue.operator;
                var filterVal = filterValue.value;

                if(filterOperator !== 'in' && Array.isArray(filterVal)) {
                    filterVal = filterVal.length > 0 ? filterVal[0] : null;
                } else if(filterOperator === 'in' && !Array.isArray(filterVal)) {
                    filterVal = filterVal.split(' OR ');
                }

                var filterIsNegated = filterValue.isNegated;
                var isFilterMatch = false;
                var itemVal = item[fieldName];

                if(filterOperator === 'eq' && filterVal !== '' && !Boolean(filterVal)) {
                    filterOperator = 'exists';
                    filterIsNegated = false;
                }

                if(Array.isArray(filterVal) && filterOperator !== 'in') {
                    filterVal = filterVal.length > 0 ? filterVal[0] : null;
                }

                if(filterValue.selectedField.type === 'date') {
                    itemVal = moment(itemVal);

                    if(itemVal) {
                        itemVal = itemVal.format('YYYY-MM-DD');
                    }

                    filterVal = moment(filterVal);

                    if(filterVal) {
                        filterVal = filterVal.format('YYYY-MM-DD');
                    }
                }

                if(filterValue.selectedField.type === 'boolean' && !Boolean(filterVal)) {
                    filterVal = true;
                    filterIsNegated = true;
                }

                if(filterOperator === 'exists') {
                    isFilterMatch = Boolean(itemVal) === Boolean(filterVal);
                } else if(filterOperator === 'contains') {
                    isFilterMatch = String(itemVal).toLowerCase().includes(filterVal.toLowerCase());
                } else if(filterOperator === 'in') {
                    isFilterMatch = filterVal && filterVal.length > 0 && _.includes(filterVal, ...(typeof(itemVal) === 'string' ? [itemVal] : itemVal));
                } else {
                    isFilterMatch = _[filterOperator](itemVal, filterVal);
                }

                isReturned &= filterIsNegated ? !isFilterMatch : isFilterMatch;
            });

            return isReturned;
        }) || [];
    }

    getFilterQuery() {
        // Contains: doc.field:value, doc.field:*value*
        // Equal: doc.field:"value" (Contains exact on text type)
        // NotNull: _exists_:doc.field

        // Gt: doc.field:[value TO *]; doc.field:>value
        // Lt: doc.field:[* TO value]; doc.field:<value
        // Gte: doc.field:>=value
        // Lte: doc.field:<=value

        // Boolean: +doc.field; -doc.field
        // Negation: NOT (...)
        // IN: (doc.field: 'a' OR doc.field: 'b' OR ...)
        // Exists: __exists__:doc.field

        var operatorByName = {
            'gt': '>',
            'gte': '>=',
            'lt': '<',
            'lte': '<='
        };

        var queryConditions = [];

        _.forEach(this.state._filterValues, (filterValue, fieldName) => {
            var filterOperator = filterValue.operator;

            if(!filterValue.isActive || filterOperator.startsWith('_')) return;

            var filterVal = filterValue.value;

            if(filterOperator !== 'in' && Array.isArray(filterVal)) {
                filterVal = filterVal.length > 0 ? sanitizeElasticFilterValue(filterVal[0]) : null;
            } else if(filterOperator === 'in' && !Array.isArray(filterVal)) {
                filterVal = _.map(filterVal.split(' OR '), (_filterVal) => {
                    return sanitizeElasticFilterValue(_filterVal);
                });
            }

            var filterIsNegated = filterValue.isNegated;

            if(filterOperator === 'eq' && filterIsNegated && !Boolean(filterVal)) {
                filterOperator = 'exists';
                filterVal = filterIsNegated;
            }

            if(filterOperator === 'exists') {
                filterIsNegated = !Boolean(filterVal);
            }

            var condition = '(';

            if(filterIsNegated) {
                condition += 'NOT ';
            }

            condition += '(';

            if(filterValue.selectedField.type === 'boolean' && !Boolean(filterVal)) {
                condition += 'NOT ';
            }

            if(filterOperator === 'in') {
                var inConditions = [];

                _.each(filterVal, (item, i) => {
                    inConditions.push('"' + item + '"');
                });

                condition += 'doc.' + fieldName + ':(' + inConditions.join(' OR ') + ')';
            } else if(filterOperator === 'exists') {
                condition += '_exists_:doc.' + fieldName;
            } else {
                condition += 'doc.' + fieldName + ':';

                if(filterOperator === 'eq') {
                    if(filterValue.selectedField.type === 'boolean') {
                        condition += 'true';
                    } else if(filterValue.selectedField.type === 'number') {
                        condition += filterVal ? parseFloat(filterVal) : '';
                    } else if(filterValue.selectedField.type === 'date') {
                        condition += filterVal ? moment(filterVal).format('YYYY-MM-DD') : '';
                    } else {
                        condition += '"' + filterVal + '"';
                    }
                } else if(filterOperator === 'contains') {
                    var _filterVal = filterVal ? String(filterVal) : '';

                    condition += '(';
                    condition += '"' + sanitizeElasticFilterValue(filterVal) + '"';
                    condition += ' OR ';
                    condition += sanitizeElasticFilterValue(_filterVal.toLowerCase());
                    condition += ' OR ';
                    condition += '*' + sanitizeElasticFilterValue(_filterVal.toLowerCase()) + '*';
                    condition += ')';
                } else if(filterValue.selectedField.type === 'date') {
                    condition += operatorByName[filterOperator] + (filterVal ? moment(filterVal).format('YYYY-MM-DD') : '');
                } else {
                    condition += operatorByName[filterOperator] + (filterVal ? sanitizeElasticFilterValue(filterVal) : '');
                }

            }

            condition += '))';

            queryConditions.push(condition);
        });

        return queryConditions.length > 0 ? queryConditions.join(' AND ') : null;
    }

    getSelectionValues(selection) {
        return selection ? _.reduce(selection, function(selectionIds, item) {
            selectionIds.push(item.id);

            return selectionIds;
        }, []) : null;
    }

    render() {
        if (this.state._error) {
            return <div>Error: {this.state._error}</div>;
        } else if (!this.isLoaded()) {
            return (
                <div style={{ position: 'relative', height: '5rem' }}>
                    <Spinner size="small" variant="base" />
                </div>
            );
        } else {
            return (
                <React.Fragment>
                    {/*<div className="slds-scrollable_y" style={{maxHeight: '50vw'}}>*/}
                        <table className="slds-table slds-table_cell-buffer slds-table_bordered slds-no-row-hover">
                            <thead>
                                <tr className="slds-line-height_reset">
                                    <th className="slds-text-title_caps" scope="col">
                                        <div className="slds-truncate" title={FIELD}>{FIELD}</div>
                                    </th>
                                    <th className="slds-text-title_caps" scope="col">
                                        <div className="slds-truncate" title={OPERATOR}>{OPERATOR}</div>
                                    </th>
                                    <th className="slds-text-title_caps" scope="col">
                                        <div className="slds-truncate" title={FILTER_VALUE}>{FILTER_VALUE}</div>
                                    </th>
                                    <th className="slds-text-title_caps" scope="col">
                                        <div className="slds-truncate" title={IS_ACTIVE}>{IS_ACTIVE}</div>
                                    </th>
                                    <th className="slds-text-title_caps" scope="col">
                                        <div className="slds-truncate"></div>
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                {/* Iterate over fields */}
                                {_.map(this.state._filterValuesList, function(filterValue, i) {
                                    var fieldName = filterValue.id;

                                    var selectedField = filterValue.selectedField || {};

                                    return (
                                        <tr className="slds-hint-parent" key={'fieldName: ' + (fieldName || '')}>
                                            <td data-label={FIELD} style={{width: '30vw'}}>
                                                {fieldName ||
                                                <GRAXFilterableCombobox
                                                    placeholder="Select Field to filter"
                                                    options={this.props.filterableFieldOptions || []}
                                                    filterOptionsHandler={(options) => {
                                                        return _.filter(options, (option, i) => {
                                                            return option && this.state._filterValuesFields.indexOf(option.id) === -1;
                                                        }) || [];
                                                    }}
                                                    selection={_.find(this.props.filterableFieldOptions, (option) => {
                                                        return option && option.id === fieldName;
                                                    }) || []}
                                                    onSelect={(event, selection) => this.onFilterValueChangeHandler(filterValue, selection)}
                                                    onRequestRemoveSelectedOption={(event, selection) => this.onFilterValueChangeHandler(filterValue, selection)}
                                                />}
                                            </td>
                                            <td data-label={OPERATOR} style={{width: '20vw'}}>
                                                {selectedField && (
                                                    (
                                                        selectedField.type === 'boolean' && TYPE_VS_OPERATORS[selectedField.type].label
                                                    ) || (
                                                        <GRAXFilterableCombobox
                                                            placeholder="Select Operator to Compare"
                                                            options={TYPE_VS_OPERATORS[selectedField.type] || []}
                                                            selection={[]}
                                                            variant="readonly"
                                                            onSelect={(event, selection) => {
                                                                selection[0].type = selection[0].type || filterValue.selectedField.type;

                                                                filterValue.operator = selection[0].id;
                                                                filterValue.isNegated = selection[0].isNegated;

                                                                if(filterValue.type !== selection[0].type) {
                                                                    filterValue.value = null;

                                                                    if(selection[0].type === 'boolean') {
                                                                        filterValue.value = false;
                                                                    }

                                                                    if(selection[0].type === 'text') {
                                                                        filterValue.value = '';
                                                                    }

                                                                    filterValue.type = selection[0].type;
                                                                }

                                                                this.onFilterValueChangeHandler(filterValue, [filterValue.selectedField])
                                                            }}
                                                        />
                                                    )
                                                )}
                                            </td>
                                            <td data-label={FILTER_VALUE} style={{width: '30vw'}}>
                                                {selectedField && (
                                                    (
                                                        (selectedField.type === 'boolean' || filterValue.operator === 'exists') && (
                                                            <Checkbox
                                                                defaultChecked={filterValue.value}
                                                                onChange={(e) => {
                                                                    filterValue.value = e.target.checked;

                                                                    this.onFilterValueChangeHandler(filterValue, [filterValue.selectedField]);
                                                                }}
                                                            />
                                                        )
                                                    ) || (
                                                        (selectedField.type === 'text' || selectedField.type === 'number' || selectedField.type === 'long') && (
                                                            <GRAXSFieldCombobox
                                                                SObjectName={this.props.SObjectType}
                                                                fieldName={selectedField.id}
                                                                onSelect={(event, selection) => {
                                                                    filterValue.value = this.getSelectionValues(selection);

                                                                    this.onFilterValueChangeHandler(filterValue, [filterValue.selectedField]);
                                                                }}
                                                                onRequestRemoveSelectedOption={(event, selection) => {
                                                                    filterValue.value = this.getSelectionValues(selection);

                                                                    this.onFilterValueChangeHandler(filterValue, [filterValue.selectedField]);
                                                                }}
                                                                elseComponent={
                                                                    <Input
                                                                        defaultValue={filterValue.value}
                                                                        assistiveText={{ label: selectedField.label }}
                                                                        id={selectedField.id}
                                                                        onBlur={(event) => {
                                                                            filterValue.value = event.target.value;

                                                                            this.onFilterValueChangeHandler(filterValue, [filterValue.selectedField]);
                                                                        }}
                                                                    />
                                                                }
                                                                onRender={(isPicklist) => {}}
                                                                multiple={filterValue.operator === 'in'}
                                                                variant={filterValue.operator === 'in' ? 'base' : 'inline-listbox'}
                                                            />
                                                        )
                                                    ) || (
                                                        selectedField.type === 'date' && (
                                                            <Datepicker
                                                                value={filterValue.value}
                                                                onChange={(event, data) => {
                                                                    filterValue.value = data.date;

                                                                    this.onFilterValueChangeHandler(filterValue, [filterValue.selectedField]);
                                                                }}
                                                                className="inline-table"
                                                                menuPosition="relative"
                                                                hasStaticAlignment={true}
                                                            />
                                                        )
                                                    )
                                                )}
                                            </td>
                                            <td data-label={IS_ACTIVE} style={{width: '10vw'}}>
                                                <Checkbox
                                                    defaultChecked={filterValue.isActive}
                                                    onChange={(e) => {
                                                        filterValue.isActive = e.target.checked;

                                                        this.onFilterValueChangeHandler(filterValue, [filterValue.selectedField]);
                                                    }}
                                                />
                                            </td>
                                            <td data-label={'Actions'} style={{width: '10vw'}}>
                                                <Button label="Remove Filter" variant="base" onClick={() => {
                                                    this.removeFieldHandler(i);
                                                }}/>
                                            </td>
                                        </tr>
                                    );
                                }.bind(this))}
                            </tbody>
                        </table>
                    {/*</div>*/}

                    <div className="slds-p-top_medium">
                        <Button label="Add Filter" variant="brand" onClick={() => {
                            this.addAnotherFieldHandler();
                        }} disabled={this.props.filterableFieldOptions == null || this.props.filterableFieldOptions.length === 0 || this.props.filterableFieldOptions.length === this.state._filterValuesFields.length} />
                    </div>
                </React.Fragment>
            );
        }
    }
}