import React from 'react';
import _ from 'lodash';

import {Spinner, Combobox} from '@salesforce/design-system-react';

import {$, baseUrl, GetParams} from '../lib/grax-data-lake';

import {comboboxFilterAndLimit} from '../lib/utils';


export default class GRAXSFieldCombobox extends React.Component {
    constructor(props) {
		super(props);

		this.state = {
            options: [],
			inputValue: '',
            selection: [],
            isLoaded: false,
            error: null
		};
    }
    
    componentDidMount() {
        $.ajax({
            type: 'GET',
            //dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake/salesforce/sobjects/' + this.props.SObjectName + '/fields/' + this.props.fieldName + GetParams({action:'load'}),
            data: {
                _select: 'name,type,picklistValues'
            },
            success: function(data) {
                this.setState({
                    options: _.map(data.picklistValues || [], (item, i) => ({
                        id: item.value,
                        label: item.label
                    })),
                    isLoaded: true
                });

                if(this.props.onLoad) {
                    this.props.onLoad(true);
                }
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(xhr, status, err);

                this.setState({
                    isLoaded: true,
                    error: err.toString()
                });

                if(this.props.onLoad) {
                    this.props.onLoad(false, err);
                }
            }.bind(this)
        });
    }

    render() {
        if (this.state.error) {
            return <div>Error: {this.state.error}</div>;
        } else if (!this.state.isLoaded) {
            return (
                <div style={{ position: 'relative', height: '2rem' }}>
                    <Spinner size="small" variant="base" />
                </div>
            );
        } else {
            if(this.state.options == null || this.state.options.length === 0) {
                if(this.props.onElseComponent) {
                    this.props.onRender(false);
                }

                return this.props.elseComponent;
            } else if(this.props.onLoad) {
                this.props.onRender(true);
            }

            return (
                <React.Fragment>
                    <Combobox
                        classNameMenu={'scrollable-GRAXSFieldCombobox'}
                        events={{
                            onChange: (event, { value }) => {
                                this.setState({
                                    inputValue: value
                                });

                                if (this.props.onChange) {
                                    this.props.onChange(event, value);
                                }
                            },
                            onRequestRemoveSelectedOption: (event, data) => {
                                this.setState({
                                    inputValue: '',
                                    selection: data.selection,
                                });

                                if (this.props.onRequestRemoveSelectedOption) {
                                    this.props.onRequestRemoveSelectedOption(event, data.selection);
                                }
                            },
                            onSelect: (event, data) => {
                                this.setState({
                                    inputValue: '',
                                    selection: data.selection,
                                });

                                if (this.props.onSelect) {
                                    this.props.onSelect(event, data.selection);
                                }
                            },
                            onRequestClose: (event, data) => {
                                if (this.props.onSelect && this.state.inputValue) {
                                    this.props.onSelect(event, [{
                                        id: this.state.inputValue,
                                        label: this.state.inputValue
                                    }]);
                                }
                            }
                        }}
                        labels={{
                            placeholder: 'Select Option',
                        }}
                        options={comboboxFilterAndLimit({
                            inputValue: this.state.inputValue,
                            limit: 1000,
                            options: this.state.options,
                            selection: this.state.selection
                        })}
                        selection={this.state.selection}
                        value={this.state.inputValue}
                        variant={this.props.variant || 'inline-listbox'}
                        multiple={this.props.multiple || false}
                        required={true}
                    />
                </React.Fragment>
            );
        }
    }
}