import React from "react";
import { Spinner, Input } from "@salesforce/design-system-react";

import SLDSSection from "./SLDSSection";
import GRAXEnvSetting from "./GRAXEnvSetting";
import { GRAXFilterableCombobox } from "./GRAXFilterGeneration";

import _ from "lodash";

const BULK_RESTORE_SETTINGS = [
  {
    name: "GRAX_ESMAXBULK",
    type: "number",
    helpText: "Enter the maximum number of records to be processed per Bulk API Batch."
  },
  {
    name: "GRAX_OBJECT_TYPES_TO_RESTORE",
    type: "text",
    helpText: "Set the API Name of Object(s) to restore, separated by comma."
  },
  {
    name: "GRAX_MAX_RESTORE",
    type: "number",
    helpText: "# of records per batch to be restored"
  }
];

export default class GRAXCloudBulkRestoreForm extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isLoading: true,
      error: null,

      globalConfigurations: {},
      title: null,
      configuration: {}
    };

    this.triggerConfigurationChange = this.triggerConfigurationChange.bind(this);
  }

  componentDidMount() {
    var globalConfigurations = this.props.globalConfigurations || {};
    var configuration = this.props.configuration || {};

    console.log("configuration", configuration);

    var dataSources = _.get(configuration, "dataSources", []);
    var dataSourcesOptions = _.map(dataSources, item => {
      return {
        id: item.organizationId,
        label: item.name + " (" + item.organizationId + ")"
      };
    });

    var bulkRestoreSourceOrganizationId = _.get(
      globalConfigurations,
      "bulkRestoreSourceOrganizationId"
    );
    var bulkRestoreDestinationOrganizationId = _.get(
      globalConfigurations,
      "bulkRestoreDestinationOrganizationId"
    );

    console.log("bulkRestoreSourceOrganizationId", bulkRestoreSourceOrganizationId);
    console.log("bulkRestoreDestinationOrganizationId", bulkRestoreDestinationOrganizationId);

    console.log("dataSourcesOptions", dataSourcesOptions);

    var sourceOrgSelection = _.filter(dataSourcesOptions, item => {
      console.log("item", item);

      return item.id === bulkRestoreSourceOrganizationId;
    });

    var destinationOrgSelection = _.filter(dataSourcesOptions, item => {
      console.log("item", item);

      return item.id === bulkRestoreDestinationOrganizationId;
    });

    console.log("destinationOrgSelection: ", destinationOrgSelection);

    this.setState({
      globalConfigurations: globalConfigurations,
      dataSourcesOptions: dataSourcesOptions,
      sourceOrgSelection: sourceOrgSelection,
      destinationOrgSelection: destinationOrgSelection,
      isLoading: false
    });
  }

  triggerConfigurationChange() {
    console.log(this.state.configuration);

    if (this.props.onChange) {
      this.props.onChange(
        this.state.title,
        this.state.configuration,
        this.state.globalConfigurations
      );
    }
  }

  render() {
    if (this.state.isLoading) {
      return <Spinner size="large" variant="brand" />;
    }

    return (
      <div className="slds-p-around_medium">
        <SLDSSection title="Configuration">
          <div className="slds-grid slds-gutters_direct slds-grid_vertical-align-center slds-p-bottom_medium">
            <div className="slds-col">
              <Input
                id="grax-cloud-bulk-restore-name"
                label="Configuration Name"
                defaultValue={_.get(this.state.configuration, "bulkRestore[0].name")}
                onBlur={e => {
                  var configuration = this.state.configuration;

                  _.set(configuration, "bulkRestore[0].name", e.target.value);

                  this.setState({
                    configuration: configuration
                  });

                  this.triggerConfigurationChange();
                }}
              />
            </div>
          </div>
        </SLDSSection>

        <SLDSSection title="Bulk Restore" className="slds-p-top_large">
          <div className="slds-form-element">
            <label className="slds-form-element__label" htmlFor="grax-cloud-bulk-process-sourceOrg">
              Source SF
            </label>
            <div className="slds-form-element__control">
              <GRAXFilterableCombobox
                placeholder="Select Source SF"
                options={this.state.dataSourcesOptions}
                selection={this.state.sourceOrgSelection}
                variant="readonly"
                onSelect={(event, selection) => {
                  if (selection != null) {
                    var globalConfigurations = this.state.globalConfigurations;

                    _.set(globalConfigurations, "bulkRestoreSourceOrganizationId", selection[0]);

                    this.setState({
                      globalConfigurations: globalConfigurations
                    });

                    this.triggerConfigurationChange();
                  }
                }}
              />
            </div>
          </div>

          <div className="slds-form-element">
            <label className="slds-form-element__label" htmlFor="grax-cloud-bulk-process-sourceOrg">
              Destination SF
            </label>
            <div className="slds-form-element__control">
              <GRAXFilterableCombobox
                placeholder="Select Destination SF"
                options={this.state.dataSourcesOptions}
                selection={this.state.destinationOrgSelection}
                variant="readonly"
                onSelect={(event, selection) => {
                  if (selection != null) {
                    var globalConfigurations = this.state.globalConfigurations;

                    _.set(
                      globalConfigurations,
                      "bulkRestoreDestinationOrganizationId",
                      selection[0]
                    );

                    this.setState({
                      globalConfigurations: globalConfigurations
                    });

                    this.triggerConfigurationChange();
                  }
                }}
              />
            </div>
          </div>

          {_.map(BULK_RESTORE_SETTINGS, (setting, i) => {
            var key = "grax-env-setting-" + (setting.id || setting.name);

            return (
              <GRAXEnvSetting
                id={key}
                key={key}
                setting={setting}
                onBlur={e => {
                  console.log(e);
                }}
              />
            );
          })}
        </SLDSSection>
      </div>
    );
  }
}
