import React from 'react';
import { Icon } from '@salesforce/design-system-react';

export default class SLDSSection extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isOpen: props.isOpen === true ? true : false || props.isCollapsible === undefined
        };

        this.handleToggle = this.handleToggle.bind(this);
    }

    handleToggle(e) {
        var isOpen = !this.state.isOpen;

        this.setState({
            isOpen: isOpen
        });

        if(this.props.onToggle) {
            this.props.onToggle(isOpen);
        }

        e.preventDefault();
    }

    render() {
        var title = <span className={'slds-truncate' + (!this.props.isCollapsible ? ' slds-p-horizontal_small' : '')} title={this.props.title}>{this.props.title}</span>;

        return (
            <div id={this.props.id || this.props.key} className={['slds-section', this.state.isOpen && 'slds-is-open', this.props.className].join(' ')}>
                <h3 className="slds-section__title slds-theme_shade">
                    {this.props.isCollapsible ? <button className="slds-button slds-section__title-action" onClick={this.handleToggle}>
                        <Icon
                            assistiveText={{ label: this.props.title }}
                            category="utility"
                            name="switch"
                            size="x-small"
                            className="slds-section__title-action-icon slds-button__icon slds-button__icon_left"
                        />
                        {title}
                    </button> : title}
                </h3>
                {this.state.isOpen && <div className="slds-section__content">
                    {this.props.children}
                </div>}
            </div>
        );
    }
}